.register-page {
  display: flex;
  width: 100%;
  height: 100vh;
  background-color: #f5f7f9;
}

.register-form-container {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  max-width: 600px;
}

.register-mockup-container {
  flex: 1;
  display: none;
  overflow: hidden;
}

.auth-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.back-to-home {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #555;
  text-decoration: none;
  transition: color 0.2s;
}

.back-to-home:hover {
  color: #5b5ef4;
}

.back-icon {
  font-size: 1.2rem;
}

.brand-logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.logo-icon {
  width: 30px;
  height: 30px;
  background-color: #5b5ef4;
  color: white;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.logo-text {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
}

.register-steps {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.step-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.step-indicator {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #e0e0e0;
  color: #777;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  margin-bottom: 0.5rem;
  transition: all 0.3s;
}

.step-container.active .step-indicator {
  background-color: #5b5ef4;
  color: white;
  box-shadow: 0 0 0 4px rgba(91, 94, 244, 0.2);
}

.step-container.completed .step-indicator {
  background-color: #4CAF50;
  color: white;
}

.step-check {
  font-size: 0.9rem;
}

.step-label {
  font-size: 0.8rem;
  color: #777;
  white-space: nowrap;
}

.step-container.active .step-label {
  color: #333;
  font-weight: 600;
}

.step-container.completed .step-label {
  color: #4CAF50;
}

.step-divider {
  flex: 1;
  height: 2px;
  background-color: #e0e0e0;
  margin: 0 0.5rem;
  position: relative;
  top: -15px;
}

.step-divider-inner {
  height: 100%;
  width: 0;
  background-color: #4CAF50;
  transition: width 0.5s;
}

.step-divider-inner.completed {
  width: 100%;
}

.auth-content {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  padding: 2rem;
  overflow: hidden;
}

.auth-error-banner {
  background-color: #ffebee;
  color: #d32f2f;
  padding: 0.8rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.error-icon {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #d32f2f;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.form-step {
  opacity: 0;
  transform: translateX(20px);
  transition: all 0.3s;
}

.form-step.animate-in {
  opacity: 1;
  transform: translateX(0);
}

.step-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #333;
}

.step-description {
  color: #777;
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-row {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.form-row .form-group {
  flex: 1;
  margin-bottom: 0;
}

label {
  display: block;
  font-size: 0.9rem;
  font-weight: 500;
  color: #555;
  margin-bottom: 0.5rem;
}

.input-container {
  position: relative;
}

.input-icon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1rem;
  color: #777;
}

.auth-input {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.auth-input:not(.textarea) {
  padding-left: 40px;
}

.auth-input:focus {
  outline: none;
  border-color: #5b5ef4;
  box-shadow: 0 0 0 3px rgba(91, 94, 244, 0.1);
}

.auth-input.error {
  border-color: #d32f2f;
}

.auth-input.textarea {
  min-height: 100px;
  resize: vertical;
}

.auth-error {
  color: #d32f2f;
  font-size: 0.8rem;
  margin-top: 0.5rem;
}

.char-counter {
  text-align: right;
  font-size: 0.8rem;
  color: #777;
  margin-top: 0.5rem;
}

.color-picker {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.color-input {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.color-input::-webkit-color-swatch {
  border: none;
  border-radius: 8px;
}

.color-preview {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.color-value {
  font-size: 0.9rem;
  color: #555;
}

.security-tips {
  background-color: #f5f7f9;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1.5rem;
}

.security-tips h4 {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  color: #555;
}

.security-tips ul {
  font-size: 0.8rem;
  color: #666;
  padding-left: 1.5rem;
}

.security-tips li {
  margin-bottom: 0.3rem;
}

.password-strength {
  margin-top: 0.8rem;
}

.strength-meter {
  height: 4px;
  background-color: #eee;
  border-radius: 2px;
  overflow: hidden;
  margin-bottom: 0.5rem;
}

.strength-fill {
  height: 100%;
  width: 0;
  transition: width 0.3s, background-color 0.3s;
}

.strength-fill.weak {
  background-color: #f44336;
}

.strength-fill.medium {
  background-color: #ff9800;
}

.strength-fill.strong {
  background-color: #4CAF50;
}

.strength-text {
  font-size: 0.8rem;
  color: #777;
}

.auth-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

.auth-button {
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.back-button {
  background-color: transparent;
  border: 1px solid #ddd;
  color: #555;
}

.back-button:hover {
  background-color: #f5f5f5;
}

.next-button {
  background-color: #5b5ef4;
  border: none;
  color: white;
}

.next-button:hover {
  background-color: #4a4dc9;
}

.next-button.loading {
  opacity: 0.7;
  cursor: not-allowed;
}

.button-icon {
  font-size: 1rem;
}

.auth-footer {
  margin-top: 2rem;
  text-align: center;
  font-size: 0.9rem;
  color: #777;
}

.auth-link {
  color: #5b5ef4;
  font-weight: 600;
  text-decoration: none;
}

.success-container {
  text-align: center;
  padding: 2rem 0;
}

.success-animation {
  margin-bottom: 2rem;
}

.checkmark-circle {
  width: 80px;
  height: 80px;
  position: relative;
  background-color: #4CAF50;
  border-radius: 50%;
  margin: 0 auto;
  animation: scale-in 0.5s forwards;
}

.checkmark {
  width: 40px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%) rotate(45deg);
  border-right: 4px solid white;
  border-bottom: 4px solid white;
  animation: check-draw 0.5s 0.5s forwards;
  opacity: 0;
}

@keyframes scale-in {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes check-draw {
  0% {
    opacity: 0;
    height: 0;
  }
  100% {
    opacity: 1;
    height: 20px;
  }
}

.success-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 0.5rem;
}

.success-message {
  color: #666;
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.success-details {
  max-width: 400px;
  margin: 0 auto;
}

.user-profile-preview {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.2rem;
  background-color: #f9f9f9;
  border-radius: 12px;
  margin-bottom: 2rem;
}

.profile-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  font-size: 1.2rem;
}

.profile-info h3 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  color: #333;
}

.profile-info p {
  font-size: 0.9rem;
  color: #777;
}

.redirect-message {
  color: #777;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.loading-dots {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.loading-dots span {
  width: 10px;
  height: 10px;
  background-color: #5b5ef4;
  border-radius: 50%;
  display: inline-block;
  animation: dot-loading 1.4s infinite ease-in-out both;
}

.loading-dots span:nth-child(2) {
  animation-delay: 0.2s;
}

.loading-dots span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes dot-loading {
  0%, 80%, 100% {
    transform: scale(0);
    opacity: 0.2;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Exit Warning Modal */
.exit-warning-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

.exit-warning-modal {
  background-color: white;
  border-radius: 12px;
  padding: 2rem;
  width: 90%;
  max-width: 450px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  animation: scaleIn 0.3s ease-out;
}

.exit-warning-modal h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.exit-warning-modal p {
  color: #666;
  margin-bottom: 1.5rem;
}

.exit-warning-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.exit-warning-buttons button {
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.exit-button {
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  color: #666;
}

.exit-button:hover {
  background-color: #eee;
}

.cancel-button {
  background-color: #5b5ef4;
  border: none;
  color: white;
}

.cancel-button:hover {
  background-color: #4a4dc9;
}

@keyframes scaleIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@media (min-width: 992px) {
  .register-mockup-container {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100vh;
    position: relative;
    background-color: #5b5ef4;
  }
  
  .register-mockup-container .mockup-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    scrollbar-width: thin;
  }
  
  .register-mockup-container .mockup-container::-webkit-scrollbar {
    width: 6px;
  }
  
  .register-mockup-container .mockup-container::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
  }
  
  .register-mockup-container .mockup-container::-webkit-scrollbar-track {
    background-color: transparent;
  }
}

.form-hint {
  font-size: 0.8rem;
  color: #777;
  margin-top: 0.5rem;
  display: block;
}

.input-container.loading .auth-input {
  padding-right: 40px;
}

.input-loading-indicator {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border: 2px solid rgba(91, 94, 244, 0.2);
  border-top: 2px solid #5b5ef4;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  0% { transform: translateY(-50%) rotate(0deg); }
  100% { transform: translateY(-50%) rotate(360deg); }
}

/* Profile Customization Styles */
.profile-customization-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.profile-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  background-color: rgba(91, 94, 244, 0.05);
  border-radius: 12px;
  margin-bottom: 1rem;
}

.profile-avatar-large {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #5b5ef4;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  transition: all 0.3s;
}

.profile-name {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.profile-username {
  font-size: 1rem;
  color: #777;
}

.form-section {
  margin-bottom: 2rem;
  border: 1px solid #eee;
  border-radius: 12px;
  padding: 1.5rem;
  background-color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.03);
}

.section-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
  color: #333;
  padding-bottom: 0.8rem;
  border-bottom: 1px solid #eee;
}

/* Avatar Options */
.avatar-options {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.avatar-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  padding: 0.8rem;
  border-radius: 8px;
  transition: all 0.2s;
}

.avatar-option.selected {
  background-color: rgba(91, 94, 244, 0.1);
}

.avatar-sample {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  transition: transform 0.2s;
}

.avatar-option:hover .avatar-sample {
  transform: scale(1.05);
}

.avatar-label {
  font-size: 0.85rem;
  color: #555;
}

/* Emoji Picker */
.emoji-picker {
  margin-top: 1rem;
}

.emoji-picker h5 {
  font-size: 0.9rem;
  margin-bottom: 0.8rem;
  color: #666;
}

.emoji-grid {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 0.5rem;
}

.emoji-item {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 6px;
  transition: all 0.2s;
}

.emoji-item:hover {
  background-color: rgba(91, 94, 244, 0.1);
  transform: scale(1.1);
}

.emoji-item.selected {
  background-color: rgba(91, 94, 244, 0.2);
}

/* Image Picker */
.image-picker {
  margin-top: 1rem;
}

.image-picker h5 {
  font-size: 0.9rem;
  margin-bottom: 0.8rem;
  color: #666;
}

.avatar-image-gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.avatar-image-option {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  position: relative;
  transition: all 0.2s;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.avatar-image-option:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.avatar-image-option.selected {
  box-shadow: 0 0 0 3px #5b5ef4;
}

.selected-check {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 25px;
  height: 25px;
  background-color: #5b5ef4;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 0.8rem;
}

/* Color Options */
.color-options {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  align-items: center;
}

.color-option {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}

.color-option:hover {
  transform: scale(1.1);
}

.color-option.selected {
  box-shadow: 0 0 0 2px white, 0 0 0 4px #5b5ef4;
}

.color-check {
  color: white;
  font-size: 1.2rem;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.color-picker-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.color-custom {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  position: relative;
  margin-bottom: 4px;
}

.color-label {
  font-size: 0.75rem;
  color: #666;
}

.color-input {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}

/* Mobile Responsiveness for Profile Section */
@media (max-width: 768px) {
  .avatar-image-gallery {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .emoji-grid {
    grid-template-columns: repeat(8, 1fr);
  }
  
  .color-options {
    gap: 0.8rem;
  }
  
  .profile-avatar-large {
    width: 100px;
    height: 100px;
    font-size: 2.2rem;
  }
}

/* Avatar hover and click styles */
.profile-avatar-large.clickable {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-hover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s;
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
  border-radius: 50%;
}

.profile-avatar-large.clickable:hover .avatar-hover-overlay {
  opacity: 1;
}

/* Avatar Modal Styles */
.avatar-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s;
}

.avatar-modal {
  background-color: white;
  border-radius: 12px;
  width: 90%;
  max-width: 550px;
  max-height: 85vh;
  overflow-y: auto;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.3);
  animation: scaleIn 0.3s;
  display: flex;
  flex-direction: column;
}

.avatar-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid #eee;
}

.avatar-modal-header h3 {
  font-size: 1.3rem;
  margin: 0;
  color: #333;
}

.close-button {
  border: none;
  background: none;
  font-size: 1.5rem;
  color: #888;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s;
}

.close-button:hover {
  background-color: #f5f5f5;
  color: #333;
}

.avatar-modal-content {
  padding: 1.5rem;
  flex: 1;
}

.avatar-type-tabs {
  display: flex;
  margin-bottom: 2rem;
  border-bottom: 1px solid #eee;
}

.avatar-tab {
  padding: 0.8rem 1.5rem;
  cursor: pointer;
  border-bottom: 3px solid transparent;
  color: #777;
  font-weight: 500;
  transition: all 0.2s;
}

.avatar-tab:hover {
  color: #5b5ef4;
}

.avatar-tab.active {
