/* Basic container styles */
.registration-mockups {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #5b5ef4, #3f3d9e);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  position: relative;
  overflow: hidden;
}

/* Main container */
.mockups-container {
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

/* Left side with text */
.mockups-left {
  flex: 1;
  padding-right: 3rem;
  animation: fadeIn 0.8s ease-out;
}

.mockups-left h2 {
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 1rem;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
}

.mockups-left p {
  font-size: 1.2rem;
  opacity: 0.9;
  max-width: 500px;
  line-height: 1.5;
}

/* Right side with mockups */
.mockups-right {
  flex: 1;
  max-width: 500px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Mockup display container */
.mockup-display {
  width: 100%;
  position: relative;
  margin-bottom: 1.5rem;
}

/* The mockup frame */
.mockup-frame {
  width: 100%;
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
  position: relative;
  color: #333;
  display: flex;
  flex-direction: column;
  animation: floatUpDown 6s ease-in-out infinite;
}

@keyframes floatUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Mockup header */
.mockup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  background: #6366f1;
  color: white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.mockup-dots {
  display: flex;
  gap: 5px;
}

.mockup-dots span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.3);
}

.mockup-dots span:nth-child(1) {
  background-color: #FF5F56;
}

.mockup-dots span:nth-child(2) {
  background-color: #FFBD2E;
}

.mockup-dots span:nth-child(3) {
  background-color: #27C93F;
}

.mockup-title {
  font-weight: 600;
  font-size: 0.9rem;
  letter-spacing: 0.5px;
}

/* Mockup body */
.mockup-body {
  flex: 1;
  padding: 1.5rem;
  background-color: #f9fafb;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* Focus timer styles */
.focus-timer {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.timer-circle {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: relative;
  background: #f1f5f9;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.timer-progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: conic-gradient(#5b5ef4 0% 75%, #e5e7eb 75% 100%);
}

.timer-display {
  position: relative;
  width: 120px;
  height: 120px;
  background: white;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 2;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.05);
}

.timer-time {
  font-size: 2rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 0.2rem;
}

.timer-label {
  font-size: 0.8rem;
  color: #6b7280;
}

/* Focus task styles */
.focus-task h3 {
  font-size: 1rem;
  color: #6b7280;
  font-weight: 500;
  margin-bottom: 0.75rem;
}

.focus-task-card {
  display: flex;
  background: white;
  border-radius: 10px;
  padding: 1.25rem;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.04), 0 0 0 1px rgba(0, 0, 0, 0.03);
  position: relative;
  overflow: hidden;
}

.focus-task-priority.high {
  width: 4px;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: #ef4444;
}

.focus-task-content {
  padding-left: 0.75rem;
}

.focus-task-name {
  display: block;
  font-weight: 600;
  font-size: 1.1rem;
  color: #1f2937;
  margin-bottom: 0.5rem;
}

.focus-task-desc {
  display: block;
  font-size: 0.9rem;
  color: #6b7280;
  line-height: 1.5;
}

/* Focus controls */
.focus-controls {
  display: flex;
  gap: 1rem;
}

.focus-button {
  flex: 1;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  font-size: 0.95rem;
}

.focus-button.pause {
  background: #f1f5f9;
  color: #4b5563;
}

.focus-button.pause:hover {
  background: #e5e7eb;
}

.focus-button.complete {
  background: #5b5ef4;
  color: white;
  box-shadow: 0 3px 8px rgba(91, 94, 244, 0.25);
}

.focus-button.complete:hover {
  background: #4f46e5;
  transform: translateY(-2px);
  box-shadow: 0 5px 12px rgba(91, 94, 244, 0.3);
}

/* Navigation dots */
.mockup-navigation {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.nav-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.4);
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.nav-dot.active {
  background-color: white;
  transform: scale(1.2);
}

/* Navigation arrows */
.mockup-arrows {
  display: flex;
  gap: 1rem;
}

.arrow-button {
  width: 36px;
  height: 36px;
  background-color: rgba(255, 255, 255, 0.15);
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.1rem;
}

.arrow-button:hover {
  background-color: rgba(255, 255, 255, 0.25);
  transform: scale(1.1);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .mockups-container {
    flex-direction: column;
    gap: 2rem;
  }

  .mockups-left {
    padding-right: 0;
    text-align: center;
    padding-bottom: 1rem;
  }

  .mockups-left p {
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .registration-mockups {
    padding: 1.5rem;
  }

  .mockups-left h2 {
    font-size: 2.2rem;
  }

  .mockups-left p {
    font-size: 1rem;
  }

  .mockup-frame {
    max-width: 90%;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  .mockups-left h2 {
    font-size: 1.8rem;
  }

  .timer-circle {
    width: 130px;
    height: 130px;
  }

  .timer-display {
    width: 100px;
    height: 100px;
  }

  .timer-time {
    font-size: 1.6rem;
  }

  .focus-task-name {
    font-size: 1rem;
  }
}

