  transition: all 0.2s ease;
}

.demo-search-box:focus-within {
  border-color: var(--primary-color, #6366f1);
  box-shadow: 0 0 0 2px rgba(99, 102, 241, 0.1);
}

.demo-search-icon {
  color: var(--gray-400, #9ca3af);
}

.demo-search-input {
  border: none;
  background: transparent;
  font-size: 0.9rem;
  color: var(--gray-700, #374151);
  outline: none;
  width: 100%;
}

.demo-add-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, var(--primary-color, #6366f1), var(--primary-dark, #4f46e5));
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 1px 3px rgba(99, 102, 241, 0.3);
}

.demo-add-btn:hover {
  box-shadow: 0 4px 10px rgba(99, 102, 241, 0.3);
  transform: translateY(-1px);
}

.demo-content-body {
  flex: 1;
  padding: 1.5rem 2rem;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* Calendar Section */
.demo-tasks-calendar {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.demo-calendar-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--gray-100, #f3f4f6);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.demo-section-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--gray-900, #111827);
  margin: 0;
}

.demo-calendar-controls {
  display: flex;
  gap: 0.5rem;
}

.demo-calendar-btn {
  padding: 0.35rem 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid var(--gray-200, #e5e7eb);
  background-color: white;
  color: var(--gray-700, #374151);
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.demo-calendar-btn:hover {
  background-color: var(--gray-50, #f9fafb);
  border-color: var(--gray-300, #d1d5db);
}

.demo-calendar-btn.active {
  background-color: var(--primary-color, #6366f1);
  color: white;
  border-color: var(--primary-color, #6366f1);
}

.demo-calendar {
  padding: 1rem;
}

.demo-calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
  margin-bottom: 10px;
}

.demo-weekday {
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--gray-500, #6b7280);
  text-align: center;
  padding: 0.5rem 0;
}

.demo-calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
}

.demo-calendar-day {
  aspect-ratio: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 8px;
  border: 1px solid transparent;
  transition: all 0.2s ease;
  cursor: pointer;
  padding: 0.5rem 0;
}

.demo-calendar-day:hover {
  background-color: var(--gray-50, #f9fafb);
  border-color: var(--gray-200, #e5e7eb);
}

.demo-calendar-day.other-month {
  opacity: 0.3;
}

.demo-calendar-day.today {
  background-color: rgba(99, 102, 241, 0.1);
  border-color: rgba(99, 102, 241, 0.3);
  font-weight: 600;
}

.demo-day-number {
  font-size: 0.9rem;
  color: var(--gray-700, #374151);
  margin-bottom: 0.25rem;
}

.demo-day-indicator {
  width: 6px;
  height: 6px;
  background-color: var(--primary-color, #6366f1);
  border-radius: 50%;
}

.demo-day-indicator.multiple {
  width: 6px;
  height: 6px;
  background-color: transparent;
  border-radius: 50%;
  position: relative;
}

.demo-day-indicator.multiple::before,
.demo-day-indicator.multiple::after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.demo-day-indicator.multiple::before {
  background-color: var(--primary-color, #6366f1);
  left: -4px;
}

.demo-day-indicator.multiple::after {
  background-color: var(--warning-color, #f59e0b);
  right: -4px;
}

/* Tasks List */
.demo-tasks-list {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.demo-list-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--gray-100, #f3f4f6);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.demo-view-filters {
  display: flex;
  gap: 0.5rem;
}

.demo-view-btn {
  padding: 0.35rem 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid var(--gray-200, #e5e7eb);
  background-color: white;
  color: var(--gray-700, #374151);
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.demo-view-btn:hover {
  background-color: var(--gray-50, #f9fafb);
  border-color: var(--gray-300, #d1d5db);
}

.demo-view-btn.active {
  background-color: var(--primary-color, #6366f1);
  color: white;
  border-color: var(--primary-color, #6366f1);
}

.demo-task-item {
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  border-bottom: 1px solid var(--gray-100, #f3f4f6);
  transition: all 0.2s ease;
  cursor: pointer;
