  font-size: 0.7rem;
}

.demo-task-content {
  flex: 1;
}

.demo-task-title {
  font-weight: 500;
  color: var(--gray-700, #374151);
  margin-bottom: 0.25rem;
}

.demo-task-meta {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 0.8rem;
}

.demo-task-priority {
  padding: 0.15rem 0.5rem;
  border-radius: 0.25rem;
  font-weight: 500;
  text-transform: capitalize;
}

.demo-task-priority.high {
  background-color: rgba(239, 68, 68, 0.1);
  color: #ef4444;
}

.demo-task-priority.medium {
  background-color: rgba(245, 158, 11, 0.1);
  color: #f59e0b;
}

.demo-task-priority.low {
  background-color: rgba(16, 185, 129, 0.1);
  color: #10b981;
}

.demo-task-due {
  color: var(--gray-500, #6b7280);
}

.demo-task-actions {
  display: flex;
  gap: 0.5rem;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.demo-task-item:hover .demo-task-actions {
  opacity: 1;
}

.demo-task-action {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  background-color: var(--gray-100, #f3f4f6);
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
}

.demo-task-action:hover {
  background-color: var(--gray-200, #e5e7eb);
}

.demo-task-action.edit:hover {
  background-color: rgba(99, 102, 241, 0.1);
  color: var(--primary-color, #6366f1);
}

.demo-task-action.delete:hover {
  background-color: rgba(239, 68, 68, 0.1);
  color: #ef4444;
}

