.profile-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-modal-container {
  background-color: white;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  animation: modal-pop 0.3s ease-out forwards;
}

@keyframes modal-pop {
  0% { transform: scale(0.9); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

.profile-modal-header {
  padding: 16px 20px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.profile-modal-header h3 {
  margin: 0;
  color: #333;
  font-size: 1.2rem;
}

.modal-close-btn {
  background: none;
  border: none;
  font-size: 24px;
  color: #888;
  cursor: pointer;
  transition: color 0.2s;
}

.modal-close-btn:hover {
  color: #333;
}

.profile-modal-tabs {
  display: flex;
  border-bottom: 1px solid #e2e8f0;
  margin-bottom: 20px;
}

.profile-tab {
  padding: 10px 20px;
  background: none;
  border: none;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  font-weight: 500;
  color: #64748b;
  transition: all 0.2s;
}

.profile-tab.active {
  color: #5b5ef4;
  border-bottom-color: #5b5ef4;
}

.profile-tab:hover:not(.active) {
  color: #334155;
  background-color: #f8fafc;
}

.profile-modal-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.profile-preview-section {
  text-align: center;
  margin-bottom: 20px;
}

.profile-preview-section h4 {
  margin-bottom: 10px;
  font-size: 0.9rem;
  color: #64748b;
}

.profile-preview {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  margin: 0 auto;
  overflow: hidden;
}

.custom-profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-options-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Icon Categories */
.icon-categories {
  display: flex;
  overflow-x: auto;
  gap: 8px;
  padding-bottom: 8px;
  margin-bottom: 10px;
}

.category-button {
  background: #f1f5f9;
  border: none;
  border-radius: 20px;
  padding: 6px 12px;
  font-size: 0.9rem;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.2s;
}

.category-button.active {
  background: #5b5ef4;
  color: white;
}

.category-button:hover:not(.active) {
  background: #e2e8f0;
}

/* Icon Grid */
.icon-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
  gap: 10px;
  max-height: 200px;
  overflow-y: auto;
  padding: 5px;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.icon-option {
  width: 40px;
  height: 40px;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  background: none;
}

.icon-option:hover {
  background-color: #f8fafc;
}

.icon-option.selected {
  border-color: #5b5ef4;
  background-color: #eff6ff;
}

/* Initials Input */
.initials-input-section {
  text-align: center;
}

.initial-input {
  width: 60px;
  height: 60px;
  border-radius: 4px;
  border: 1px solid #e2e8f0;
  text-align: center;
  font-size: 2rem;
  margin: 15px 0;
}

.help-text {
  font-size: 0.9rem;
  color: #64748b;
  margin-top: 10px;
}

/* Upload Section */
.upload-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.upload-btn, .remove-image-btn {
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
}

.upload-btn {
  background-color: #5b5ef4;
  color: white;
  border: none;
}

.remove-image-btn {
  background-color: transparent;
  color: #ef4444;
  border: 1px solid #ef4444;
}

.upload-help-text {
  font-size: 0.9rem;
  color: #64748b;
}

/* Color Options */
.color-options-section h4 {
  margin-bottom: 10px;
  font-size: 0.9rem;
  color: #64748b;
}

.color-options {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(32px, 1fr));
  gap: 8px;
  max-height: 120px;
  overflow-y: auto;
  padding: 5px;
}

.color-option {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid transparent;
  transition: transform 0.2s;
}

.color-option:hover {
  transform: scale(1.1);
}

.color-option.selected {
  border-color: #1e293b;
  transform: scale(1.1);
}

/* Modal Footer */
.profile-modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #e2e8f0;
}

.modal-cancel-btn, .modal-save-btn {
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
}

.modal-cancel-btn {
  background-color: transparent;
  border: 1px solid #e2e8f0;
  color: #64748b;
}

.modal-save-btn {
  background-color: #5b5ef4;
  border: none;
  color: white;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .profile-modal-container {
    width: 95%;
    max-height: 80vh;
  }

  .icon-grid {
    grid-template-columns: repeat(auto-fill, minmax(36px, 1fr));
    max-height: 160px;
  }

  .icon-option {
    width: 36px;
    height: 36px;
    font-size: 1.2rem;
  }

  .color-options {
    grid-template-columns: repeat(auto-fill, minmax(28px, 1fr));
  }

  .color-option {
    width: 28px;
    height: 28px;
  }
}
