/* Modern Footer styles */
.landing-footer {
  background-color: var(--gray-800, #1f2937);
  color: white;
  padding: 0;
  width: 100%;
}

.footer-main {
  display: flex;
  justify-content: space-between;
  padding: var(--space-xl, 2.5rem);
  flex-wrap: wrap;
  gap: var(--space-xl, 2.5rem);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: var(--space-sm, 0.75rem);
  font-weight: 700;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.footer-logo .logo-bubble {
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, var(--primary-color, #6366f1), var(--primary-dark, #4f46e5));
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.footer-links {
  display: flex;
  gap: var(--space-xl, 2.5rem);
  flex-wrap: wrap;
}

.footer-column {
  min-width: 150px;
}

.footer-column h4 {
  color: white;
  font-weight: 600;
  margin-bottom: var(--space-md, 1rem);
  font-size: 1.1rem;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-column ul li {
  margin-bottom: var(--space-sm, 0.75rem);
}

.footer-column ul li a {
  color: var(--gray-400, #9ca3af);
  text-decoration: none;
  transition: all 0.2s ease;
  font-size: 0.95rem;
}

.footer-column ul li a:hover {
  color: white;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-lg, 1.5rem) var(--space-xl, 2.5rem);
  flex-wrap: wrap;
  gap: var(--space-lg, 1.5rem);
}

.footer-info {
  font-size: 0.9rem;
  opacity: 0.8;
}

.footer-info p {
  margin-bottom: var(--space-xs, 0.5rem);
}

.footer-social {
  display: flex;
  gap: var(--space-lg, 1.5rem);
}

.social-icon {
  color: var(--gray-400, #9ca3af);
  text-decoration: none;
  transition: all 0.2s ease;
  font-size: 1.2rem;
}

.social-icon:hover {
  color: white;
  transform: translateY(-2px);
}

.footer-legal {
  display: flex;
  gap: var(--space-md, 1rem);
  flex-wrap: wrap;
  font-size: 0.85rem;
}

.footer-legal a {
  color: var(--gray-400, #9ca3af);
  text-decoration: none;
  transition: color 0.2s ease;
}

.footer-legal a:hover {
  color: white;
  text-decoration: underline;
}

.footer-divider {
  color: var(--gray-600, #4b5563);
}

/* Responsive styles */
@media (max-width: 768px) {
  .footer-main {
    flex-direction: column;
    gap: var(--space-lg, 1.5rem);
    padding: var(--space-lg, 1.5rem);
  }

  .footer-links {
    flex-direction: column;
    gap: var(--space-lg, 1.5rem);
  }

  .footer-bottom {
    flex-direction: column-reverse;
    text-align: center;
    padding: var(--space-lg, 1.5rem);
  }

  .footer-social {
    justify-content: center;
  }

  .footer-legal {
    justify-content: center;
  }
}