  font-size: 0.95rem;
  padding: 8px 16px;
  border-radius: 30px;
  margin-bottom: var(--space-md);
  border: 1px solid rgba(99, 102, 241, 0.2);
  box-shadow: 0 2px 10px rgba(99, 102, 241, 0.1);
  position: relative;
  overflow: hidden;
}

