  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255,255,255,0.3), rgba(255,255,255,0));
  z-index: 1;
}

.hero-tag-icon {
  margin-right: 8px;
  font-size: 1.1rem;
  position: relative;
  z-index: 2;
}

.hero-tag-text {
  position: relative;
