  max-width: 620px;
  line-height: 1.5;
  font-weight: 400;
}

/* Add these styles to your Hero.css file */

.feature-pills-container {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-md);
  margin-top: var(--space-lg);
}

.feature-pill {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: var(--radius-full);
  font-weight: 500;
  font-size: 0.9rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.feature-pill:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.12);
}

.free-pill {
  background: linear-gradient(135deg, #5b5ef4 0%, #7a7cf5 100%);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.private-pill {
  background: white;
  color: var(--gray-800);
  border: 1px solid var(--gray-200);
}

.simple-pill {
  background: #f9f9ff;
  color: var(--primary-color);
  border: 1px solid rgba(91, 94, 244, 0.2);
}

.pill-icon {
  font-size: 1.1rem;
}
